<template>
    <div id="nav">
      <ul>
        <router-link to="/" class="links" :class="{'link-active': getLinkStyle(0)}">/home</router-link>
        <router-link to="/work" class="links" :class="{'link-active': getLinkStyle(1)}">/work</router-link>
        <router-link to="/contact" class="links" :class="{'link-active': getLinkStyle(2)}">/contact</router-link>
      </ul>
    </div>
</template>

<script>
export default {
	props: ['index'],
	methods: {
		getLinkStyle(linkIndex) {
			console.log('getting class');
			if (this.index === linkIndex.toString()) return true;
			return false;
		}
	}
};
</script>

<style scoped>
#nav {
	margin-top: 30px;
	font-size: 14px;
	text-align: center;
}

ul {
	margin: 0 auto;
}

.links {
	color: white;
	margin-right: 24px;
}

.links:hover {
	color: rgb(29, 29, 29);
}

.link-active {
	font-weight: bold;
	text-decoration: none;
	font-size: 16px;
}
</style>



