<template>
  <div class="container">
      <div id="contact-container">
          <p id="details">
         i'm available for freelance or remote full-time gigs. to talk to me about something interesting you can send an email to 
         <a class="link" href="mailto:clintonmbah44@gmail.com">clintonmbah44@gmail.com</a> or direct message me on twitter  
         <a class="link" href="https://twitter.com/mclint_">@mclint_</a></p>
        <navbar index="2"></navbar>
      </div>
  </div>
</template>

<script>
import Navbar from './Navbar.vue';
export default {
	created() {
		var root = document.querySelector(':root');
		root.style.setProperty('--bgColor', '#1c2131');
		root.style.setProperty('--containerColor', '#2a324b');
	},
	components: { Navbar }
};
</script>

<style>
#contact-container {
	width: 25%;
}

.link {
	color: #c7ccdb;
}
</style>



