<template>
  <div class="container">
    <div id="home-container">
      <p id="greeting">Hi! <span id="name">I'm Mbah Clinton.</span></p>
      <p id="brief">Full-stack software engineer. Accra, Ghana.</p>
      <p id="details">softie. computer nerd with (yuuuge) muscles.
         i make cool things that run on the <span class="technologies">web</span>, <span class="technologies">android</span> and <span class="technologies">iOS</span> with
        <span class="technologies">vue.js</span>, <span class="technologies">asp.net</span>, <span class="technologies">node.js</span>,
         and <span class="technologies">xamarin</span>
    </p>
    <navbar index="0"></navbar>
    </div>
  </div>
</template>

<script scoped>
import Navbar from './Navbar.vue';
export default {
	created() {
		var root = document.querySelector(':root');
		root.style.setProperty('--bgColor', '#0d686d');
		root.style.setProperty('--containerColor', '#119da4');
	},
	components: { Navbar }
};
</script>


<style scoped>
#home-container {
	width: 25%;
}

#greeting {
	font-size: 30px;
	font-weight: bold;
}

#name {
	font-weight: normal;
	color: rgba(255, 255, 255, 0.54);
}

#brief {
	font-size: 16px;
	color: rgba(255, 255, 255, 0.54);
	margin-top: 8px;
}

.technologies {
	color: #aafac8;
}

.link {
	color: #aafac8;
}
</style>



