<template>
    <div class="container">
        <div id="work-container">
            <p id="details">
                whenever i'm not playing need for speed: payback or grand theft auto v, i'm either working
                on my open source android app <a href="https://play.google.com/store/apps/details?id=com.alansa.ideabag2" class="link">programming ideas</a> or my multiplayer 
                trivia twitter bot <a href="https://tbg-bot.netlify.com/" class="link">@ttbg_bot</a> or
                painting portraits showcased on my instagram at <a href="https://instagram.com/m.clint" class="link">@m.clint</a> or any other of my side-projects that 
                you can find on <a href="https://github.com/m.clint" class="link">github</a>
            </p>
            <navbar index="1"></navbar>
        </div>
    </div>
</template>

<script>
import Navbar from './Navbar.vue';
export default {
	created() {
		var root = document.querySelector(':root');
		root.style.setProperty('--bgColor', 'rgb(192, 125, 0)');
		root.style.setProperty('--containerColor', 'orange');
	},
	components: { Navbar }
};
</script>

<style scoped>
#work-container {
	width: 25%;
}

.link {
	color: orangered;
}
</style>



